.App {
  font-family: sans-serif;
  text-align: center;
}

.masonry-grid {
  display: -webkit-box;
  display: -ms-flexbox; 
  display: flex;
  margin-left: -30px;
  width: auto;
}
.masonry-grid_column {
  padding-left: 30px;
  background-clip: padding-box;
}

.masonry-grid_column > div { 
  background: grey;
  margin-bottom: 30px;
  border: none;
  border-radius: none;
}

/* TODO: fix overflow data */
.masonry-grid_column .card section {
  visibility: hidden;
  display: flex;
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: lightgray;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 1em;
  cursor: pointer;
  opacity: 0;
  transition: visibility 0s, opacity 0.15s ease;
}

.dark-mode .masonry-grid_column .card section {
  background-color: #333;
  color: white;
}

.masonry-grid_column .card:hover section {
  visibility: visible;
  opacity: 0.9;
}

.artwork-modal .modal-dialog .modal-content {
  height: 100%;
}

.modal-header.dark-mode,
.modal-body.dark-mode {
  background-color: #333;
  color: white;
  border-color: black;
}

.modal-header.dark-mode *,
.modal-body.dark-mode * {
  border-color: black;
}

.modal-body.dark-mode button {
  color: white;
}

.modal-body.dark-mode button.active {
  background-color: #444;
  color: white;
  border-color: black;
}

.artwork-modal .modal-dialog .modal-content img {
  max-width: 100%;
}

/* Modal Image Height Adjustments */
.artwork-modal .modal-body {
  height: calc(100% - 4.5rem);
}

.artwork-modal .tab-content {
  padding: 1rem;
  max-height: 75%;
}

.artwork-modal .modal-body .container,
.artwork-modal .modal-body .row,
.artwork-modal .modal-body .container .row div:nth-child(1) {
  height: 100%;
}

.artwork-modal .modal-body .container img {
  max-height: 100%;
}

.artwork-modal .modal-body .container .row > div:nth-child(1) {
  text-align: center;
  justify-content: center;
}

.artwork-modal .modal-body .container .row > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 100%;
}

.artwork-modal .modal-body .container .row > div:nth-child(2) {
  overflow-y: auto;
  overflow-x: hidden;
  position: relative;
}

#App {
  padding-top: 56px;
  padding-bottom: 30px;
}

#App.dark-mode {
  background-color: #333;
}

#App > .container {
  padding-top: 30px;
}

#Nav .navbar-nav {
  display: flex;
  width: 100%;
}

#Nav .navbar-text {
  width: 20.5%; 
}

#Nav form {
  width: 50%;
}

#Nav.bg-dark * {
  color: white;
}

#InfoBubble {
  position: fixed;
  bottom: 3.5rem;
  left: 3.5rem;
  background-color: #b50938;
  color: white;
  font-weight: bold;
  padding: 1rem 1.5rem;
  border-radius: 100%;
  cursor: pointer;
  transition: all 0.3s ease;
}

#InfoBubble:hover {
  padding: 1.25rem 1.75rem;
  bottom: 3.25rem;
  left: 3.25rem;
}

#InfoBubble > span {
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

.info-modal .tab-content {
  padding: 1rem;
}